/* COLORS */
$white: #ffffff;
$beige-bright: #A89B94;
$beige-dark: #746861;
$dark-blue: #2F4858;
$dark-green: #2F474B;
$brown: #A66A5E;

* {
	font-family: "Heebo", Helvetica, sans-serif;
	font-weight: 300;
	line-height: 1.5;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.hello-world {
	color: white;
	text-shadow: 0px 0px 2px $dark-blue;
}